<template>
  <div v-if="showHistorySideBar" class="ait-flex-none ait-w-[400px] ait-mb-5">
    <div class="ait-w-[400px] ait-p-5 ait-card ait-bg-[var(--ait-option-card-bg-color)] ait-h-full ait-ml-0 ait-rounded-l-none">
      <div class="ait-logo ait-flex">
        <div class="ait-logo-text">
          <div class="ait-font-bold ait-text-base ait-pt-2 ait-mb-1">History</div>
        </div>
      </div>
      <div v-if="!isUserChatExists" class="ait-py-6 ait-px-3 ait-flex ait-h-[98%] ait-justify-center ait-items-center">
        <div class="ait-flex ait-flex-col">
          <img :src="`${constants.extension_dir}assets/images/no-data-planet.png`" alt="no data">
          <label class="ait-flex ait-justify-center ait-text-base">No Data</label>
        </div>
      </div>
      <div v-if="isUserChatExists" class="ait-py-6 ait-pr-6 ait-flex ait-h-[96%] ait-flex-col ait-overflow-y-auto">
        <div v-for="(chat,key) in ai_chat">
          <div v-if="chat.role === 'user'" class="ait-card ait-p-3 ait-flex ait-flex-row ait-justify-between">
            <label>{{ chat.title ?? chat.item }}</label>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script lang="ts">
import AiChat from '../vue/AiChat';


export default {
  extends: AiChat,
  data() {
    return {
      showHistorySideBar: false,
    };
  },
  computed: {
    isUserChatExists() {
      let isExists = false;

      if (!this.ai_chat) {
        return isExists;
      }

      Object.keys(this.ai_chat).map((key) => {
        if (this.ai_chat[key].role === "user") {
          isExists = true;
          return;
        }
      });

      return isExists;
    },
  },
};
</script>
