<template>
  <div id="ait-tab-0-image"
    class="ait-bg-[var(--ait-option-card-bg-color)] ait-flex-initial ait-w-full ait-p-4 ait-rounded-none ait-mr-0">
    <div class="ait-flex ait-responsive-zoom ait-flex-row ait-justify-between">
      <div class="ait-grid ait-grid-cols-1 md:ait-grid-cols-2">
        <div class="ait-flex">
          <SvgLogo name="logo" className="ait-w-8 ait-h-8 ait-mt-2 ait-mr-2"/>
          <h1 class="ait-font-bold ait-leading-[50px] ait-text-2xl">{{ __("Image") }}</h1>
        </div>
        <label
          class="ait-hidden md:ait-flex md:ait-ml-2 ait-items-center ait-space-x-3 ait-text-sm ait-relative dark:ait-text-neutral-200 ait-border ait-rounded-xl ait-cursor-pointer">
          <select name="choose_ai_model" id="choose_ai_model"
            class="ait-bg-transparent ait-w-fit ait-outline-none ait-p-1.5"
            style="-webkit-appearance: none!important; appearance: none!important;" v-model="selectedAIModel"
            v-on:change="this.changeStorage('settings.image_ai_model', (selectedAIModel ?? 'Stable Diffusion'))" disabled>
            <option v-for="(model, key) in this.image_ai_models" :value="key">{{ model }}</option>
          </select>
        </label>
      </div>
      <div class="ait-flex ait-flex-row ait-items-center ait-justify-center">
        <button @click="onEditToolsClick" type="button" style="border-radius: 24px !important; background-color:#fb992d"
          class="ait-whitespace-nowrap ait-flex ait-text-white ait-font-bold ait-py-2 ait-px-4 hover:ait-opacity-90 active:ait-opacity-80">
          <svg-logo name="magic_wand" /> {{ __('Editing Tools') }} </button>
        <button v-on:click="this.newChat(null, 'ai_single', Helpers.SHA1('image'))" type="button"
          style="border-radius: 24px !important;"
          class="ait-whitespace-nowrap ait-flex ait-bg-[var(--ait-tab-menu-active-bg-color)] ait-text-white ait-font-bold ait-py-2 ait-px-4 ait-ml-2 hover:ait-opacity-90 active:ait-opacity-80">
          <svg-logo class="ait-mr-1" name="reference_image_icon" /> {{ __('New Image') }} </button>
        <button type="button" @click="toggleImageChatSettingsSection" class="ait-hidden md:ait-block md:ait-ml-4 svg-logo">
          <svg-logo id="image_chat_settings_icon" name="image_chat_settings_icon" />
        </button>
      </div>
    </div>
    <div class="ait-py-2 md:ait-py-5">
      <hr />
    </div>
    <div id="aitopia-sidebar">
      <div id="ai-sidebar" class="ait-flex ait-w-full ait-h-full ait-overflow-hidden">
        <div class="ait-flex ait-flex-col ait-w-full">
          <Painter mode="full" />
        </div>
      </div>
    </div>
  </div>
</template>

<script lang="ts">
import AiChat from '../vue/AiChat';

export default {
  extends: AiChat,
  data() {
    return {
      image_ai_models: {
        StableDiffusion: 'Stable Diffusion',
      },
      selectedAIModel: 'StableDiffusion',
    };
  },
  methods: {
    onEditToolsClick() {
      if (this.showEditToolUploadModal) {
        this.showEditToolUploadModal = false;
      }

      localStorage.setItem('reference_edit_tool_image', null);
      localStorage.setItem('is_edit_modal_redirected_from_painter', "1");
      this.showEditToolsModal = true;
    },
    toggleImageChatSettingsSection() {
      const imageChatSettings = document.getElementById('full-screen-image-settings');

      if (!imageChatSettings) {
        return;
      }

      if (imageChatSettings.classList.contains('ait-show')) {
        imageChatSettings.classList.remove('ait-show');
        imageChatSettings.classList.add('ait-hide');
        this.changeSectionsRoundedValue(imageChatSettings);
        return;
      }

      if (imageChatSettings.classList.contains('ait-hide')) {
        imageChatSettings.classList.remove('ait-hide');
        imageChatSettings.classList.add('ait-show');
        this.changeSectionsRoundedValue(imageChatSettings);

      }
    },
    changeSectionsRoundedValue(imageChatSettings: HTMLElement) {
      const section = document.getElementById('ait-tab-0-image');
      if (!section && !imageChatSettings) {
        return;
      }

      if (imageChatSettings.classList.contains('ait-show')) {
        section.classList.remove("ait-rounded-r-lg");
        section.classList.add("ait-rounded-r-none");
        return;
      }

      section.classList.remove("ait-rounded-r-none");
      section.classList.add("ait-rounded-r-lg");
    }
  },
};
</script>
    