<template>
  <div id="ait-tab-0-chat"
       class="ait-bg-[var(--ait-option-card-bg-color)] ait-flex-initial ait-w-full ait-p-4 ait-mr-0 ait-rounded-none">
    <div class="ait-flex ait-flex-row ait-responsive-zoom ait-justify-between">
      <div class="ait-grid ait-grid-cols-1 md:ait-grid-cols-2">
        <div class="ait-flex">
          <SvgLogo name="logo" className="ait-w-8 ait-h-8 ait-mt-2 ait-mr-2"/>
          <h1 class="ait-font-bold ait-leading-[50px] ait-text-2xl">{{ __("Chat") }}</h1>
        </div>
        <label
            class="ait-hidden md:ait-flex ait-items-center md:ait-ml-4 ait-space-x-3 ait-text-sm ait-relative dark:ait-text-neutral-200 ait-border ait-rounded-xl ait-cursor-pointer">
          <img src="/public/assets/images/gpt-outline.png" alt="gpt image"
               class="ait-w-5 ait-h-5 ait-absolute ait-left-[10px]">
          <select name="choose_gpt" id="choose_gpt" class="ait-bg-transparent ait-w-fit ait-outline-none"
                  style="padding: 5px 30px 5px 20px; margin-right: 8px !important;" v-model="this.settings.ai_model"
                  v-on:change="this.changeStorage('settings.ai_model', (this.settings.ai_model ?? 'GPT-3.5'));">
            <option v-for="(key, model) in this.ai_models" :value="key" selected>{{ model }}</option>
          </select>
        </label>
      </div>
      <div class="ait-flex ait-items-center ait-max-h-[50px]">
        <button class="ait-whitespace-nowrap ait-flex ait-items-center ait-space-x-2 ait-bg-transparent hover:ait-bg-[var(--ait-link-color)] ait-transition-all ait-rounded-full ait-p-1 ait-px-2">
          <svg xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24" stroke-width="1.5"
               stroke="currentColor" aria-hidden="true" class="ait-w-4 ait-h-4 dark:ait-text-gray-200">
            <path stroke-linecap="round" stroke-linejoin="round" d="M12 4.5v15m7.5-7.5h-15"></path>
          </svg>
          <span class="ait-text-sm dark:ait-text-gray-200 ait-whitespace-nowrap"
                v-on:click="this.newChat()">{{ __("New Conversation") }}</span>
        </button>
      </div>
    </div>
    <div class="ait-py-2 md:ait-py-5">
      <hr/>
    </div>
    <div id="aitopia-sidebar">
      <div id="ai-sidebar" class="ait-flex ait-w-full ait-h-full ait-overflow-hidden">
        <div class="ait-flex ait-flex-col ait-w-full">
          <InnerContentView mode="full"/>
        </div>
      </div>
    </div>
  </div>
</template>

<script lang="ts">
import AiChat from '../vue/AiChat';

export default {
  extends: AiChat,
  mounted() {
    this.changeSectionsRoundedValue();
  },
  methods: {
    toggleHistory() {
      this.showHistorySideBar = !this.showHistorySideBar;
      this.changeSectionsRoundedValue();
    },
    changeSectionsRoundedValue() {
      const section = document.getElementById('ait-tab-0-chat');
      if (section) {
        if (this.showHistorySideBar) {
          section.classList.remove("ait-rounded-l-lg sm:ait-rounded-r-lg");
          section.classList.add("ait-rounded-r-none");
        } else {
          section.classList.remove("ait-rounded-r-none");
          section.classList.add("ait-rounded-l-lg sm:ait-rounded-r-lg");
        }
      }
    }
  },
};
</script>
    