import {createApp} from 'vue'
import {Helpers} from "./classes/Helpers";
import AiChat from "./vue/AiChat";
import './assets/css/style.css'
import './assets/css/tailwind.css'
import 'vue-toast-notification/dist/theme-sugar.css'
import {methods} from "./classes/Methods";
import HelloWorld from "./components/HelloWorld.vue";
import FullScreen from "./pages/FullScreen.vue";
import AiResponse from "./components/AiResponse.vue";
import Navigation from "./components/Navigation.vue";
import Switchbar from "./components/Switchbar.vue";
import SvgLogo from "./components/SvgLogo.vue";
import Credit from "./components/Credit.vue";
import Prompts from "./components/Prompts.vue";
import Write from "./components/Write.vue";
import PDFUpload from "./components/PDFUpload.vue";
import ImageChatSettings from "./components/ImageChatSettings.vue";
import ImageChatSettingsModal from "./components/ImageChatSettingsModal.vue";
import InnerContentView from "./components/InnerContentView.vue";
import ReferenceImage from "./components/ReferenceImage.vue";
import ImagePreview from "./components/ImagePreview.vue";
import EditToolsModal from "./components/EditToolsModal.vue";
import MakeAReview from "./components/MakeAReview.vue";
import Painter from "./components/Painter.vue";
import EditToolsUploadModal from "./components/EditToolsUploadModal.vue";
import EditToolsUploadModalActionSection from "./components/EditToolsUploadModalActionSection.vue";
import {FontAwesomeIcon} from '@fortawesome/vue-fontawesome'
import {library} from '@fortawesome/fontawesome-svg-core'
import {fas} from '@fortawesome/free-solid-svg-icons'
import {far} from '@fortawesome/free-regular-svg-icons'
import {AiApi} from "./classes/AiApi";
import FileButton from "./components/FileButton.vue";
import InnerReferenceImage from "./components/InnerReferenceImage.vue";
import MakeAReviewModal from "./components/MakeAReviewModal.vue";
import InnerImagePreview from "./components/InnerImagePreview.vue";
import UserInfoSection from "./components/UserInfoSection.vue";
import HistorySideBar from "./components/HistorySideBar.vue";
import SingleResponse from "./components/SingleResponse.vue";
import FullScreenChatSection from "./components/FullScreenChatSection.vue";
import FullScreenImageSection from "./components/FullScreenImageSection.vue";
import FullScreenSearchSection from "./components/FullScreenSearchSection.vue";
import Modal from "./components/Modal.vue";
import HoverPopover from "./components/HoverPopover.vue";
import VisionToolbar from "./components/VisionToolbar.vue";
import VisionImagePreview from "./components/VisionImagePreview.vue";
import InviteFriends from "./components/InviteFriends.vue";
import UpgradePlanModal from "./components/UpgradePlanModal.vue";

library.add(fas)
library.add(far)

/*End Component*/
let sidebar = document.createElement("div");
sidebar.id = "aitopia-container";

let main = document.createElement("div");
main.id = "aitopia";
main.classList.add("aitopia");
main.append(sidebar);
let body = document.querySelector("body");
let body_id = body.getAttribute('id') ?? '';
if (body_id.indexOf('aitopia') == -1) {
    let ele = "html";
    body = document.querySelector(ele);
}
body?.append(main);

/* Create Vue */
AiChat.template = FullScreen;
AiChat.methods = methods;
AiChat.extends = AiApi;
var VueApp = window['aiVueApp'] = createApp(AiChat)
.component('Navigation', Navigation)
.component('AiResponse', AiResponse)
.component('HelloWorld', HelloWorld)
.component('Switchbar', Switchbar)
.component('Credit', Credit)
.component('font-awesome-icon', FontAwesomeIcon)
.component('SvgLogo', SvgLogo)
.component('FileButton', FileButton)
.component('ImageChatSettings', ImageChatSettings)
.component('InnerContentView', InnerContentView)
.component('Prompts', Prompts)
.component('Write', Write)
.component('MakeAReview', MakeAReview)
.component('EditToolsModal', EditToolsModal)
.component('EditToolsUploadModal', EditToolsUploadModal)
.component('EditToolsUploadModalActionSection', EditToolsUploadModalActionSection)
.component('Painter', Painter)
.component('ReferenceImage', ReferenceImage)
.component('ImageChatSettingsModal', ImageChatSettingsModal)
.component('ImagePreview', ImagePreview)
.component('InnerReferenceImage', InnerReferenceImage)
.component('PDFUpload', PDFUpload)
.component('MakeAReviewModal', MakeAReviewModal)
.component('InnerImagePreview', InnerImagePreview)
.component('UserInfoSection', UserInfoSection)
.component('HistorySideBar', HistorySideBar)
.component('SingleResponse', SingleResponse)
.component('FullScreenChatSection', FullScreenChatSection)
.component('FullScreenImageSection', FullScreenImageSection)
.component('Modal', Modal)
.component('HoverPopover', HoverPopover)
.component('VisionToolbar', VisionToolbar)
.component('VisionImagePreview', VisionImagePreview)
.component('InviteFriends', InviteFriends)
.component('FullScreenSearchSection', FullScreenSearchSection)
.component('UpgradePlanModal', UpgradePlanModal)
.mount("#aitopia");

let container = document?.querySelector("#aitopia-sidebar");
Helpers.resizeBorder(container, 440);