<script lang="ts">
import AiChat from '../vue/AiChat';
import {defineComponent} from "vue";

export default defineComponent({
  extends: AiChat,
  name: 'UserInfoSection',
  data() {
    return {
      fullScreenCredit: null,
      fullScreenExtraCredit: null,
      queryTitle: null,
    };
  },
  created() {
    this.$watch(() => this.credit_details, this.setCredit);
  },
  mounted() {
    this.setCredit();
  },
  methods: {

    setCredit() {
      const element = document.getElementById('full-tab-menu');
      if (element) {
        const imageTab = element.getElementsByTagName("li")[3];

        this.getQueryTitle(imageTab);
        this.getCredit(imageTab);

        element.addEventListener('click', () => {
          this.getQueryTitle(imageTab);
          this.getCredit(imageTab);
        });
      }
    },

    getCredit(element: HTMLLIElement) {
      if (element.getAttribute('aria-label') === 'ait-tab-0-image' && element.classList.contains('active')) {
        return this.getCreditByType('image');
      }

      return this.getCreditByType();
    },
    getCreditByType(type?: string) {
      if (this.credit_details && ((this.credit_details ?? {})[(type ?? this.settings.ai_model)] ?? false) && (this.credit_details[(type ?? this.settings.ai_model)]['credit'] ?? 0) >= 0) {
        this.fullScreenCredit = this.credit_details[(type ?? this.settings.ai_model)]['balance'] ?? 10;
        this.fullScreenExtraCredit = this.credit_details[(type ?? this.settings.ai_model)]['extra_credit'] ?? 0;
        return;
      }

      this.fullScreenExtraCredit = 0;
      this.fullScreenCredit = "∞";
    },
    getQueryTitle(element: HTMLLIElement) {
      if (element.getAttribute('aria-label') === 'ait-tab-0-image' && element.classList.contains('active')) {
        this.queryTitle = "Image Queries";
        return;
      }

      this.queryTitle = "Text Queries";
    },
    checkEmailLength(email: string) {
      if (email && email.length > 18) {
        return `${email.substring(0, 18)}...`;
      }

      return email;
    }
  }
});
</script>

