    <svg-logo className="ait-w-5 ait-h-5" name="gift_white_icon"/>
    <p class="ait-hidden ait-text-white sm:ait-flex ait-align-text-bottom ait-text-xs">{{ __('Friends & Earn Credits') }}</p>
  </div>
  <div class="ait-hidden sm:ait-block ait-w-full ait-py-5 ait-px-6 ait-card ait-mb-4 ait-bg-[var(--ait-option-card-bg-color)] ait-mx-auto">
    <label class="ait-block dark:ait-text-white ait-text-sm ait-font-medium ait-mb-4">{{ this.queryTitle }}</label>
    <div class="ait-flex ait-flex-col">
