        <label class="ait-block dark:ait-text-white ait-text-xs ait-font-small ait-mb-2">{{
            this.fullScreenCredit + ' ' +
            __('left')
          }}</label>
      </div>
      <div class="ait-w-full">
        <div class="ait-w-full ait-bg-gray-200 ait-rounded-full ait-h-2.5 ait-mb-4 dark:ait-bg-gray-700">
          <div class="ait-bg-green-600 ait-h-2.5 ait-rounded-full dark:ait-bg-green-500"
               :style="'max-width:100%;transition: width 2s;' + `width:${(this.fullScreenCredit / 1000) * 100}%`">
          </div>
        </div>
      </div>
      <div class="ait-flex ait-flex-row ait-justify-between">
        <label class="ait-block dark:ait-text-white ait-text-xs ait-font-small ait-mb-2">{{ __('Extra: ') }}</label>
        <label
            class="ait-block dark:ait-text-white ait-text-xs ait-font-small ait-mb-2">{{
            this.fullScreenExtraCredit + ' ' +
            __('left')
          }}</label>
      </div>
      <div class="ait-w-full">
        <div class="ait-w-full ait-bg-gray-200 ait-rounded-full ait-h-2.5 ait-mb-4 dark:ait-bg-gray-700">
          <div class="ait-bg-green-600 ait-h-2.5 ait-rounded-full dark:ait-bg-green-500"
               :style="'max-width:100%;transition: width 2s;' + `width:${(this.fullScreenExtraCredit / 1000) * 100}%`">
          </div>
        </div>
      </div>
    </div>
    <button title="Go premium to get more" type="button" class="aitopia-pricing ait-relative ait-my-2 ait-h-8 ait-w-full ait-text-white ait-border-none ait-rounded-full ait-text-wrap ait-bg-[var(--ait-tab-menu-active-bg-color)] hover:ait-opacity-90 active:ait-opacity-80">
      <div class="relative ait-text-sm ait-text-white">
        {{ __('Upgrade Plan') }}
      </div>
    </button>
    <div class="ait-flex ait-flex-row ait-justify-between ait-mt-3">
      <div class="ait-cursor-pointer ait-flex ait-flex-row aitopia-login">
        <font-awesome-icon icon="fa-solid fa-user" class="ait-h-4 ait-w-4 ait-mr-1 ait-cursor-pointer"/>
        <label class="ait-flex ait-cursor-pointer">{{ checkEmailLength(this.email) }}</label>
      </div>
      <div class="ait-switchbar-tab-item ait-rounded-lg ait-cursor-pointer arc-edge aitopia-options ait-ml-1">
        <font-awesome-icon icon="fa-solid fa-gear" class="ait-h-4 ait-w-4"/>
      </div>
    </div>
    <div class="ait-flex ait-flex-row ait-justify-between ait-mt-5" v-on:click="Helpers.openPage({url: (constants.extension_dir + 'src/html/setup.html?side-bar-option=true')});">
      <div class="ait-cursor-pointer ait-flex ait-flex-row">
