        <label class="ait-flex ait-ml-2">{{ __('Back to Sidebar') }}</label>
      </div>
    </div>
  </div>
  
  <!-- Mobile View -->
  <div class="bottom-area ait-show sm:ait-hidden sm:ait-mt-auto ait-flex ait-flex-col ait-justify-center ait-items-center ait-gap-3 ait-mb-[10px] sm:ait-mb-[14px] md:ait-mb-[4px]">
    <div class="ait-flex ait-flex-row ait-justify-between ait-mt-5" v-on:click="Helpers.openPage({url: (constants.extension_dir + 'src/html/setup.html?side-bar-option=true')});">
