    <div class="ait-flex ait-flex-col ait-gap-1 ait-cursor-pointer">
      <div class="ait-switchbar-tab-item ait-rounded-lg ait-p-1 ait-items-center ait-flex ait-flex-col ait-cursor-pointer" data-dropdown-toggle="ait-invite-friends">
        <svg-logo className="ait-w-4 ait-h-4" name="gift_icon"/>
        <label class="ait-cursor-pointer ait-text-center ait-text-balance" style="font-size: 10px;">{{ __(`Invite & Earn`) }}</label>
