    </div>
    <div class="ait-flex ait-flex-col ait-gap-2">
      <div class="ait-switchbar-tab-item ait-rounded-lg ait-p-2 ait-cursor-pointer arc-edge aitopia-options">
        <font-awesome-icon icon="fa-solid fa-gear" class="ait-h-4 ait-w-4"/>
      </div>
    </div>
    <div class="ait-cursor-pointer">
      <font-awesome-icon icon="fa-solid fa-user" class="ait-h-4 ait-w-4 aitopia-login"/>
    </div>
  </div>
</template>
